.sequencer-container {
  background-color: orange;
  border-radius: 2vh;
  position: relative;
  box-shadow: 0.5vh 0.5vh 10px rgba(46, 46, 46, 0.5);
}

.sequencer-header {
  height: 10%;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 2.5vh
}

.header-element {
  display: inline;
  padding-left: 1vh;
  padding-right: 1vh;
}

.header-element.instrument {
  height: 80%;
}

.header-element.sec {
  padding-left: 0;
}

.header-element.left {
  float: left;
}

.header-element.right {
  margin-left: auto;
  font-size: 200%;
}

.sequencer {
  position: relative;
  margin: auto;
  width: 100%;
  height: 85%;
}

.grid-container {
  float: left;
  position: relative;
  overflow: scroll;
}

.grid {
  float: left;
  display: table;
  table-layout: fixed;
  border-spacing: 0;
}

.grid-row {
  display: table-row;
}

.grid-item {
  display: table-cell;
}

.back-cell {
  border: 1px solid black;
  box-sizing: border-box;
  background-color: rgb(83, 106, 122);
}

.preview-note {
  position: absolute;
  background-color: dodgerblue;
  box-sizing: border-box;
  border-radius: 5px;
  border: 1px solid black;
  opacity: 0.4;
}

.notes {
  display: inline;
  position: relative;
  top: 0;
  left: 0;
}

.note {
  position: absolute;
  background-color: dodgerblue;
  box-sizing: border-box;
  border-radius: 5px;
  border: 1px solid black;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
  transition: box-shadow 0.5s, background-color 0.5s
}

.note-selected {
  border: 2px solid white;
}

.note:hover {
  background-color: rgb(110, 164, 255);
  box-shadow: 4px 4px 5px rgba(0, 0, 0, 0.5);
  transition: box-shadow 0.5s, background-color 0.5s
}

.note-right {
  position: absolute;
  left: 85%;
  width: 15%;
  height: 100%;
  cursor: col-resize;
}

.keyboard-container {
  float: left;
  overflow-y: hidden;
}

.key-container {
  position: relative;
  width: 100%;
}

.key {
  position: absolute;
  box-sizing: border-box;
  border: 1px solid black;
  text-align: right;
  user-select: none;
}

.w-key {
  width: 100%;
  background-color: white;
  z-index: 1;
}

.w-key.pressed {
  background-color: lightgray;
}

.b-key {
  width: 50%;
  background-color: black;
  z-index: 2;
  color: white;
}

.b-key.pressed {
  background-color: dimgray;
}

.caret {
  position: sticky;
  top: 0;
}


